// Content
.content {
    padding: 80px 60px;
    text-align: center;
    h2 {
        font: {
            family: 'Montserrat', sans-serif; 
            size: 40px;
            weight: 300;
        }
        margin-bottom: 80px;
        letter-spacing: 1px;
        @media #{$mediaExSmall} {
            font-size: 35px
        }
        @media #{$mediaSmall} {
            font-size: 36px
        }
        @media #{$mediaMedium} {
            font-size: 37px
        }
    }
    .price-offer {
        &:nth-child(2) {
            .badge {
                background-color: #5eb3b7;
                padding: 6px 12px;
                color: #fff;
                text-transform: uppercase;
                letter-spacing: 1px;
                margin-bottom: 10px;
                margin-top: -40px
            }
            h3 { margin-top: 0px }
            border-left: 1px solid #d0d0d0;
            border-right: 1px solid #d0d0d0;
            @media #{$mediaExSmall} {
                border: none
            }
            @media #{$mediaSmall} {
                border: none
            }
            @media #{$mediaMedium} {
                border: none
            }
        }
        h3 {
            font: {
                size: 30px;
                weight: 300;
            }
            margin-bottom: 20px;
            margin-top: 40px;
            letter-spacing: 1px;
            text-transform: capitalize;
            @media #{$mediaExSmall} {
                font-size: 25px
            }
            @media #{$mediaSmall} {
                font-size: 26px
            }
            @media #{$mediaMedium} {
                font-size: 27px
            }
        }
        .price {
            font: {
                size: 30px;
                weight: 300
            }
            margin-bottom: 20px;
            @media #{$mediaExSmall} {
                font-size: 25px
            }
            @media #{$mediaSmall} {
                font-size: 26px
            }
            @media #{$mediaMedium} {
                font-size: 27px
            }
            .amount {
                font-size: 60px;
                @media #{$mediaExSmall} {
                    font-size: 50px
                }
                @media #{$mediaSmall} {
                    font-size: 52px
                }
                @media #{$mediaMedium} {
                    font-size: 54px
                }
            }
            sup {
                top: -1.5em
            }
        }
        .price-txt {
            margin-bottom: 30px;
            p {
                margin: 0;
                font-size: 16px;
                letter-spacing: 1px;
                font-weight: 600;
                @media #{$mediaExSmall} {
                    font-size: 14px
                }
                @media #{$mediaSmall} {
                    font-size: 14px
                }
                @media #{$mediaMedium} {
                    font-size: 15px
                }
            }
            span {
                display: block;
                font-size: 14px;
                color: #666;
                letter-spacing: 1px;
                text-transform: capitalize;
                @media #{$mediaExSmall} {
                    font-size: 12px
                }
                @media #{$mediaSmall} {
                    font-size: 12px
                }
                @media #{$mediaMedium} {
                    font-size: 13px
                }
            }
        }
    }
}