// Navigation Bar
.navbar {
    background-color: $mainColor !important;
    padding: 20px;
    .navbar-brand {
        color: #fff;
        font: {
            family: 'Montserrat', sans-serif;
            size: 20px;
            weight: 600
        }
        transition: .2s;
        &:hover {
            color: $brandColor;
        }
        @media #{$mediaExSmall} {
            font-size: 17px
        }
        @media #{$mediaSmall} {
            font-size: 18px
        }
        @media #{$mediaMedium} {
            font-size: 19px
        }
    }

    .nam {
        display: inline-block;
        color: #fff;
        padding-left: 40px;
        font-size: 14px;
        font-weight: 600;
        @media #{$mediaExSmall} {
            font-size: 12px;
            padding-left: 10px;
        }
        @media #{$mediaSmall} {
            font-size: 12px;
            padding-left: 15px;
        }
        @media #{$mediaMedium} {
            font-size: 13px;
            padding-left: 20px;
        }
    }

    .navbar-toggler {
        color: #fff !important;
        border-color: #fff !important
    }

    .collapse {
        .navbar-nav {
            text-align: center;
            @media #{$mediaExSmall} {
                padding-top: 40px;
            }
            @media #{$mediaSmall} {
                padding-top: 40px;
            }
            @media #{$mediaMedium} {
                padding-top: 40px;
            }
            .nav-item {
                position: relative;
                display: block;
                color: #fff;
                font: {
                    size: 14px;
                }
                letter-spacing: 1px;
                text-transform: capitalize;
                transition: .3s;
                border-bottom: 2px solid transparent;
                margin: 0 10px;
                z-index: 1;
                cursor: pointer;
                @media #{$mediaExSmall} {
                    margin-bottom: 20px;
                    font-size: 12px
                }
                @media #{$mediaSmall} {
                    margin-bottom: 20px;
                    font-size: 12px
                }
                @media #{$mediaMedium} {
                    margin-bottom: 20px;
                    font-size: 13px
                }
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    border-bottom: 2px solid #fff;
                    transform: scaleY(2);
                    opacity: 0;
                    transition: .3s;
                }
                &:hover {
                    &:before {
                        transform: scaleY(1);
                        opacity: 1;
                    }
                }
                .nav-link {
                    color: rgba(255, 255, 255, .9);
                }
            }
        }
        .icons {
            width: 25px;
            height: 50px;
            position: relative;
            top: 13px;
            margin-left: 20px;
            .icon {
                display: block;
                width: 100%;
                height: 4px;
                background: #fff;
                position: absolute;
                top: 0;
                border-radius: 10px;
                &:nth-child(2) {
                    top: 10px
                }
                &:nth-child(3) {
                    top: 20px;
                }
            }
        }
    }
}